// Mixins //
@mixin homeBody {
  // display: grid;
  // grid-template-columns: 1fr 1fr;
  width: 80vw;
  margin: auto;
  padding-top: 20px;
  // grid-gap: 50px;
  // margin-bottom: 20px;

  img {
    // height: 100%;
    // width: 500px;
    width: 100%;
    height: auto;
    margin-top: 18px;
    max-height: 500px;
    margin: auto;
    object-fit: cover;
    overflow: hidden;
  }

  .home_text {
    width: auto;
  }

  .button:hover {
    box-shadow: 2px 4px 5px 1px $main_grey;
    transition: box-shadow 0.3s ease-in-out;
  }
}

@mixin homeHeader {
  display: flex;
  height: 50px;
  align-items: center;
  box-shadow: 1px 3px 5px 1px $main_grey;

  .header {
    color: $lt_grey;
    padding-left: 20px;
  }
}

@mixin button {
  border: 0;
  border-radius: 5px;
  color: white;
  padding: 10px 24px;
  text-align: center;
  font-size: 18px;
}

$lt_grey: #f0f0f0;
$main_grey: #9da7ac;
$finance: #70d4a4;
$relationships: #64c0b2;
$employment: #61bcd6;
$spirituality: #5aaed5;
$health: #519feb;

// Style //

.app-wrapper {
  display: flex;
  width: 100vw;
}

.home_mission {
  @include homeBody;
  text-align: center;

  .home_mission_photo {
    overflow: hidden;
    width: 100%;
    max-height: 400px;
    object-fit: cover;
    height: auto;
    margin: auto;
  }

  .home_mission_text {
    align-content: left;
  }
}
.home_wrapper {
  margin: 30px 0;
}
.home_finance {
  @include homeBody;

  .home_text {
    padding-left: 10px;
  }

  .home_finance_photo {
    display: flex;
    justify-content: center;
  }

  .finance_button {
    display: flex;
    justify-content: flex-end;
    margin-right: 65px;

    .button {
      background-color: $finance;
      @include button;
    }
  }
}

.finance_button a {
  text-decoration: none;
  color: $lt_grey;
  font-size: 1.2em;
}

.finance_header {
  @include homeHeader;
  background-color: $finance;
}

