$lt_grey: #f0f0f0;
$main_grey: #9da7ac;
$finance: #70d4a4;
$relationships: #64c0b2;
$employment: #61bcd6;
$spirituality: #5aaed5;
$health: #519feb;
@import "home";

.about_wrapper {
  display: grid;
  grid-template-columns: 1fr;
  width: 100vw;
}

.about_body {
  justify-items: center;
  width: 80vw;
  margin: auto;
}

.page_title {
  display: flex;
  justify-content: center;
  color: $lt_grey;
  font-size: 40px;
}

.about_section_one {
  display: flex;
  @include homeBody;
  justify-items: center;
}

.about_image_one {
  width: 100%;
  height: auto;
  overflow: hidden;
}

// .about_image_one img {
//   margin: -100px 0px -200px 0px;
// }

.about_text_two {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  justify-content: center;
}

.about_image_two {
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.about_image_two {
  width: 100%;
  height: auto;
  max-height: 400px;
  overflow: hidden;

  img {
    margin: auto;
    object-fit: cover;
    overflow: hidden;
  }
}

.about_text_three {
  width: 80vw;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr;
}
