// Mixins //
@mixin homeBody {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 80vw;
  padding-top: 20px;
  grid-gap: 50px;
  margin-bottom: 20px;

  img {
    height: 100%;
    width: 500px;
    margin-top: 18px;
  }
  .home_text {
    width: 600px;
  }

  .button {
    border: none;
    border-radius: 5px;
    color: white;
    padding: 10px 24px;
    text-align: center;
    font-size: 18px;
  }
  .button:hover {
    box-shadow: 2px 4px 5px 1px $main_grey;
    transition: box-shadow 0.3s ease-in-out;
  }
}

@mixin homeHeader {
  display: flex;
  height: 50px;
  align-items: center;
  box-shadow: 1px 3px 5px 1px $main_grey;
  .header {
    color: $lt_grey;
    padding-left: 20px;
  }
}

$lt_grey: #f0f0f0;
$main_grey: #9da7ac;
$finance: #70d4a4;
$relationships: #64c0b2;
$employment: #61bcd6;
$spirituality: #5aaed5;
$health: #519feb;

.home_container {
  display: grid;
  grid-template-columns: 1fr;
  width: 100vw;
}

.home_top {
  margin-right: 1175px;
  font-size: 3em;
  margin-left: -50px;
  color: $main_grey;
}

.home_top:hover {
  color: $finance;
  box-shadow: $main_grey;
  transition: box-shadow 0.3s ease-in-out;
}

.navigation_wrapper {
  max-width: 100vw;
  background-color: rgba($color: #f0f0f0, $alpha: 0.5);
  position: fixed;
  display: flex;
  justify-content: flex-end;
  padding-right: 150px;

  .nav_links {
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-items: center;
    text-decoration: none;
  }

  .nav_links a {
    text-decoration: none;
    color: black;
  }

  .nav_link_wrapper {
    font-size: 1.1em;
    padding-left: 10px;
    padding-right: 10px;
    text-decoration: none;

    .login_button {
      border: none;
      border-radius: 5px;
      color: white;
      padding: 3px 9px;
      text-align: center;
      font-size: 18px;
      background-color: $finance;
      margin: 10px;
    }
    .login_button:hover {
      box-shadow: 2px 4px 5px 1px $main_grey;
      transition: box-shadow 0.3s ease-in-out;
    }
  }
}

.navigation_wrapper:hover {
  background-color: rgba($color: #f0f0f0, $alpha: 0.8);
  transition: box-shadow 1s ease-in-out;
}

.hero_image img {
  // margin-top: -46px;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100vw;
}
.home_hero_links {
  height: 40px;
  display: flex;
  width: 80vw;
  padding-left: 83px;
  justify-content: start;
  border-bottom: none;
}

.hero_link {
  font-weight: 400;
  display: grid;
  padding-right: 40px;
  font-size: 18px;
  padding-left: 20px;
  background-color: $lt_grey;
  align-items: center;
  border-bottom: none;
}

.hero_link:hover {
  cursor: pointer;
  color: $finance;
  text-decoration: underline;
  transition: ease-in-out;
}

.main_menu {
  cursor: pointer;
  font-weight: 550;
  margin-right: 6%;
  font-size: 20px;
}

.main_menu_blog {
  margin-top: -20px;
}

@media (max-width: 800px) {
  .home_hero_links {
    padding: 0;
    margin: auto;
    width: 100%;
    // display: block;
    justify-content: center;
  }
  .hero_link {
    padding: 0 5px;
    // border-left: .5px solid black;
  }
}

