.pillar_body_wrapper {
  display: grid;
  grid-template-columns: 1fr;

  p {
    padding: 10px;
  }
}

.pillar_color_bar {
  height: 40px;
  background-color: $finance;
  box-shadow: 1px 3px 5px 1px $main_grey;
}

.pillar_section_one {
  //   display: grid;
  //   grid-gap: 30px;
  //   grid-template-columns: 1fr 1fr;
  text-align: center;

  .pillar_section_one_image {
    img {
      max-width: 500px;
      height: auto;
    }
  }

  .pillar_text_one {
    align-content: left;
  }
}

.right_arrow_wrapper {
  display: flex;
  // position: relative;
  padding-top: 650px;
  justify-items: center;
  float: right;
  color: $main_grey;
}

.right_arrow_container {
  width: 200px;
  display: grid;
  justify-items: center;
}

.right_arrow {
  display: grid;
  justify-content: right;
  font-size: 4em;
}
.right_arrow a:hover {
  color: $finance;
  transition: ease-in 0.5s;
}

.right_arrow_text {
  display: grid;
  justify-content: right;
  font-size: 2em;
}

.right_arrow_container a {
  color: $main_grey;
  text-decoration: none;
}

