.pillar_blog_section {
  display: grid;
  justify-items: center;
  width: 80vw;
  margin: auto;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
}

.pillar_preview_container {
  width: 250px;
  height: 500px;
  display: grid;
  grid-template-columns: 1fr;
}

.pillar_preview_image {
  width: 250px;
  height: 250px;
}

.pillar_preview_title {
  margin-top: 10px;
  text-align: center;
}

.pillar_preview_description {
  width: 250px;
  margin-top: 10px;
  margin-bottom: 40px;
  overflow: hidden;
}
